.extras__scroll-to-top {
  height: 50px;
  width: auto;
  opacity: 0;
  visibility: hidden;
  cursor: pointer;
  z-index: 1;
  background-color: #f8d24e;
  border: 2px solid #131313;
  border-radius: 50px;
  padding: 5px;
  font-family: Seymour One, sans-serif;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  animation: extras__scroll-to-top 1s linear infinite both;
  position: fixed;
  bottom: 20px;
  right: 20px;
}

.extras__scroll-to-top:hover {
  color: #fcfcfc;
  background: #0a8817;
}

.extras__scroll-to-top.active {
  opacity: 1;
  visibility: visible;
}

@keyframes extras__scroll-to-top {
  0% {
    transform: translate(0);
  }

  20% {
    transform: translate(-2px, 2px);
  }

  40% {
    transform: translate(-2px, -2px);
  }

  60% {
    transform: translate(2px, 2px);
  }

  80% {
    transform: translate(2px, -2px);
  }

  100% {
    transform: translate(0);
  }
}

.color-change-3x {
  animation: color-change-3x 5s infinite alternate both;
}

@keyframes color-change-3x {
  0% {
    background: rgba(130, 234, 26, .5);
  }

  50% {
    background: rgba(46, 105, 255, .5);
  }

  100% {
    background: rgba(235, 51, 255, .5);
  }
}

.side-dotted-menu {
